.navbar {
    padding: 0 !important;
    margin: 30px !important;
    top: -20px;
    border-radius: 10px;
    /* margin: 10px; */
    background-color: #e7f8ee !important;
    border: 0.7px solid #a0d3b4;
}

.navbar-nav {
    right: 130px;
    gap: 50px;
}

.nav-link {
    font-size: 20px !important;
    color: #20c997;
}

.nav-link:hover {
    color: #20c997 !important;
    border-bottom: 2px solid #5bb37c;
}

.service-text {
    font-size: 20px !important;
    margin-top: 13px;
}

.logo {
    height: 130px;
    padding-left: 20px;
}

.dropdown-menu {
    font-size: 13px;
    width: 212px;
    box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: none !important;
    /* height: 112px; */
    /* background-color: transparent !important; */
}

.dropdown-menu li {
    padding-top: 10px;
}
